@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,500;0,600;0,700;1,100&display=swap);
body {
  margin: 0;
  /*
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.title-bar{
    height: 30x;
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: white;
    align-items: center;
    box-shadow:  1px 3px rgba(0,0,0,.1);
    
    font-weight: 700; 
    font-size: 1rem;
    color: #222;

    position: fixed;
    z-index: 1;
    top: 0;
}

.title-bar-left{
    display: flex;
}

.title-bar-right{
    display: flex;
}

.menu-button{
    height: 30px;
    padding: 18px 5px 18px 30px;
    font-size: 30px;
}
.menu-options{
    display: flex;
    justify-content: center;
    overflow-x: hidden;
}

.icon-options{
    margin-top: 10px;
}

.icon-option{
    padding: 10px;
}
.title-sticker-row{
    justify-content: right;
    align-items: right;
}

.title-sticker{
    margin-right: -5px;
}

.menu-item{
    padding: 10px;
    margin-top: 15px;
}

.title-logo{
  height: 30px;
  padding: 1rem;
}

.arrow-logo{
    box-shadow: 0 1px 1px 0 hsla(0,0%,49%,.5);
}
.sticker{
    background-color: rgb(255, 238, 0);
    height: 50px;
    width: 50px;
    border-radius: 50%;
    box-shadow: 0 1px 1px 0 hsla(0,0%,49%,.5);

    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    font-weight: 900;

    transform: rotate(-40deg);
}
.quiz{
    display: flex;
    padding: 90px 30px 30px 75px;
}

.quiz-section{
    flex: 65% 1;
}
.ad-title{
    color: grey;
    letter-spacing: 5px;;
}

.second-ad{
    margin-top: 450px;
}
.ad-section{
    height: 100%;
    flex: 35% 1;
    padding-top: 150px;

    cursor: pointer;

    overflow-x: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.top-sticker{
    margin-bottom: 20px;
}

.quiz-details{
    font-size: .875rem;
}

.mid-dot{
    margin: 0 .5rem;
    font-weight: 900;
    text-align: center;
}

.contributor-avatar{
    border-radius: 50%;
    height: 50px;
    width: 50px;
}

.credits-line{
    display: flex;
}

.credits-text{
    font-size: 12px;
    padding: 9px;
}

.question-box{
    display: flex; 
    flex-wrap: wrap;
    width: 100%;
    padding: 30px 30px 30px 0px;
    justify-content: center;
    align-items: center;
}
.question{
    flex: 100% 1;

    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: center;

    height: 220px;
    padding: 5px;
    border-radius: 5px;
    color: white;
    font-weight: 900;
    font-size: 40px;
}
.option{
    flex: 40% 1;

    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: center;

    padding: 5px;
    width: 40%;
    height: 200px;
    border-radius: 5px;
    color: white;
    font-weight: 900;
    font-size: 30px;
}

.option:hover{
    cursor: pointer;
}

.option-not-selected{
    opacity: 60%;
}

.option-not-selected:hover{
    cursor: default;
}

.option-0-col{
    margin: 20px 20px 0px 0px;
}

.option-1-col{
    margin: 20px 0px 0px 0px;
}
.result-card{
    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: left;
    flex-direction: column;

    width: 100%;
    margin: 30px 30px 30px 0px;
    height: 500px;
    border-radius: 3px;
    background-image: linear-gradient(180deg, #e40c78 0%, #e32 51.44%, #e40c78 100%);
}
.result-card-header{
    flex: 10% 1;
    color: white;
    width: 100%;
    margin-left: 30px;
    display: flex; 
    align-items: center;
}

.result-card-title{
    margin-left: 20px;
}
.result-card-desc{
    line-height: 1.4;
}
.result-image{
    height: 275px;
    width: auto;
}
.result-card-inner{
    background-color: white;
    border-radius: 3px;
    margin: 0px 30px 30px 30px;
    padding: 20px;
    width: 90%;
    flex: 100% 1;
    display: flex;
}

.result-card-inner-left{
    flex: 40% 1;
    display: flex;
    align-items: flex-start;
}

.result-card-inner-right{
    flex: 60% 1;
    margin-left: 10px;
}

.buttons-row{
    width: 95%;
    cursor: pointer;
    color: white;
    margin-bottom: 30px;
}

